import React, { Component } from 'react';

const GetPontoCadastro = (ponto) => fetch(`${process.env['REACT_APP_BASE_URL']}/CadastraPonto/GetPonto`, {
    method: 'post',
    body: JSON.stringify(ponto),
    headers: new Headers({
        'content-type': 'application/json',
        'Authorization': 'Bearer ' + sessionStorage.getItem("loginStore"),
    })
})
    .then(x => {
        if (x.status === 200) return x.json()
        return
    })


const PostPontoCadastro = (ponto) => fetch(`${process.env['REACT_APP_BASE_URL']}/CadastraPonto/PostPonto`, {
    method: 'post',
    body: JSON.stringify(ponto),
    headers: new Headers({
        'content-type': 'application/json',
        'Authorization': 'Bearer ' + sessionStorage.getItem("loginStore"),
    })
})
    .then(x => x.json())
    .then((x) => x)




class CadastroPonto extends Component {
    state = {
        pontos: [],
        icones: []
    }

    envia() {

        PostPontoCadastro({
            NomePonto: document.getElementById('NomePonto').value,
            Latitude: document.getElementById("Latitude").value,
            Longitude: document.getElementById("Longitude").value,
            IconeId: document.getElementById("IconeId").value

        })
            .then(x => {
                console.log(x)
                GetPontoCadastro().then(x => {

                    this.setState({
                        pontos: x.pontos,
                        icones: x.icones
                    });
                    console.log(x);
                })
            })


            

    }


    componentDidMount() {
        GetPontoCadastro().then(x => {

            this.setState({
                pontos: x.pontos,
                icones: x.icones
            });
            console.log(x);
        })

    }

    render() {

        return <div style={{ backgroundColor: 'white' }}>


            <form onSubmit={(event) => {
                event.preventDefault();

                this.envia()
            }}>
                <input type="text" id="NomePonto" placeholder="Nome Ponto" />
                <input type="text" id="Latitude" placeholder="Latitude" />
                <input type="text" id="Longitude" placeholder="Longitude" />
                <select id="IconeId" onChange={data=>console.log(data.target.selectedIndex)}>
                    {this.state.icones.map(data=>{
                        return <option value={data.idIcone}>{data.idIcone} -- {data.nomeIcone}</option>
                
                    })}
          
                </select>
                <button type="submit">ENVIO</button>
            </form>

            {this.state.pontos.map(x => <div key={x.idPonto}><span>{x.idPonto} -- {x.nomePonto}</span><br /></div>)}
        </div>;
    }
}

export default CadastroPonto;